import React, { useEffect } from 'react'

const Alert = ({text, type, removeAlert,list}) => {
  useEffect(()=>{
    let showingAlert = setTimeout(() => {
      removeAlert() 
    }, 3000);
    return ()=> clearTimeout(showingAlert);
  },[list])
  return (
    <p className={`alert alert-${type}`} >{text}</p>
  )
}

export default Alert
